import './App.css';
import React from 'react';
import StartScreen from "./startScreen";
import HomeScreen from "./homescreen"
import { BrowserRouter, Routes, Route } from "react-router-dom";
class App extends React.Component {
  render() {
    return (
      <BrowserRouter>
        <Routes>
          <Route path="/Smoke-Away/" element={<StartScreen />}>
          </Route>
          <Route path="/Smoke-Away/homescreen" element={<HomeScreen />}>
          </Route>
        </Routes>
      </BrowserRouter>
    );
  }
}

export default App;
